
/* Styling on Elements Direct */
a{
  color:rgb(58, 58, 58);
  text-decoration: none !important;
}

a:hover{
  color:rgb(68, 68, 253);
}

p{
  font-size:16px;
  font-family: 'Roboto Mono', monospace;
}


/* Major Parts */ 
.App {
  text-align: center;
  margin:0px;
  padding:0px;
}

.Grid{
  margin:0px;
  padding:0px;
}


.logo{
  /* background: #ff0000; */
  color:rgb(36, 36, 36);
  font-size:20px;
  height:20px;
  /* line-height:3px; */
  text-align:center;
  /* text-shadow:1px 1px black; */
  font-family: 'Kumar One Outline', cursive;
  border-radius:1px;
}

.tagline{
  text-align:center;
}

.headingContainer{
  background-color:rgb(255, 217, 0);
}

.heading{
  padding-top:50px;
  font-size:50px;
  font-family: 'Bangers', cursive;
  text-align:center;
  color:rgb(126, 126, 126)
}

.subHeading{
  font-size:22px;
  font-family: 'Oswald', sans-serif;
}

/* .mainImage{
  width:30%;
} */

.articleHeading{
  font-family: 'Oswald', sans-serif;
  font-size:30px;
  font-style:italic;
  color:white;
  margin-bottom:20px;
  padding:10px;
  background-color:grey;
}

/* Skills */

.langFrame{
  /* border-style: solid;
  border-color: rgb(231, 231, 231);
  border-width: 0px 1px 0px 1px; */
}

.skill{
  text-align:center;
  font-size:24px;
  font-family: 'Oswald', sans-serif;
}

.skillImg{
  margin-left:auto;
  margin-right:auto;
  max-width:15%;
}

/*  Projects */
.project{
  
}

.column{
  /* border-style: solid; */
  /* border-color: rgb(231, 231, 231); */
  /* border-width: 0px 1px 1px 1px; */
}

.projectStory{
  margin:10px;
}

.projectHeading{
  font-size:24px;
  font-family: 'Oswald', sans-serif;
  margin-top:0px;
  text-align:center;
  font-weight: bold;
}

.projectImg{
  width:50%;
  margin-right:auto;
  margin-left:auto;
}

.projectText{
  text-align:center;
}

.projectShort{

}

/* .sponsor{
  border-color: rgb(245, 0, 0);
  border-width: 1px;
} */

.bottomAdImg{
  width:50%;
  margin-right:auto;
  margin-left:auto;
}

.bottomAdHead{
  text-align:center;
  width:50%;
  margin-right:auto;
  margin-left:auto;
}

.bottomAdCompany{
  text-align:center;
}

.copyRight{
  color:grey;
  width:50%;
  margin-right:auto;
  margin-left:auto;
  text-align:center;
}


@media (min-width: 768px) {  
  .logo{
    font-size:60px;
    height:60px;
  }
  .heading{
    font-size:130px;
  }
  .subHeading{
    font-size:30px;
  }
  .projectHeading{
    text-align:left;
  }
  .projectImg{
    width:100%;
  }
  .projectText{
    text-align:left;
  }
  .bottomAdImg{
    width:100%;
  }
  
  .bottomAdHead{
    text-align:left;
    width:100%;
  }
  
  .bottomAdCompany{
    text-align:left;
  }
  
}
